import React from 'react';
import {MapLayer} from 'react-leaflet'
import L from 'leaflet'
import 'leaflet-routing-machine'

class Routing extends React.Component {

    constructor(props) {
        //console.log("Routing component", props);
        super();

        this.initializeRouting = this.initializeRouting.bind(this);
        this.destroyRouting = this.destroyRouting.bind(this);
    }

    componentDidUpdate() {
        this.initializeRouting();
    }

    componentWillUnmount() {
        this.destroyRouting();
    }

    initializeRouting() {
        console.log("Routing component", this.props);
        if (!this.props.map || this.route) {
            return;
        }

        const {map, from, to} = this.props;

        const plan = new L.Routing.Plan([
            L.latLng(from[0], from[1]),
            L.latLng(to[0], to[1])
          ], {
            routeWhileDragging: false,
            //geocoder: L.Control.Geocoder.nominatim(),
          });

        this.route = L.Routing.control({
            plan
        });

        map.leafletElement.addControl(this.route);
    }

    componentWillReceiveProps_org(nextProps) {
        console.log("Routing component", nextProps);

        if (nextProps.map === null) {
            return;
        }

        const {map, from, to} = nextProps
        map.leafletElement = L.Routing.control({
        position: 'topleft',
        waypoints: [
            L.latLng(from[0], from[1]),
            L.latLng(to[0], to[1])
        ]
        }).addTo(map)

    }

    /*componentWillMount() {
        super.componentWillMount()
        const {map, from, to} = this.props
        this.leafletElement = L.Routing.control({
        position: 'topleft',
        waypoints: [
            L.latLng(from[0], from[1]),
            L.latLng(to[0], to[1])
        ]
        }).addTo(map)
    }*/

    destroyRouting() {
        if (this.props.map) {
            console.log("Destroy route....");
          this.props.map.leafletElement.removeControl(this.route);
        }
      }

    render() {
        return null;
    }
}

export default Routing;